

import { defineComponent } from 'vue';

export default defineComponent({
    name: 'Botao',
    props: {
        name: String,
        url: String,
        theme: {
            type: String,
            default: 'btn',
            btn2: 'btn2',
            consultar: 'consultar',
            limpar: 'limpar',
        }
    },
    data() {
        return {}
    },
    methods: {
        handleClick() {

            this.$emit('limpar');
        },
        getSelection() {
            this.$emit('consultar');
        }
    }


})
